@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

@keyframes fade {
  0%{
    background-color:red;
  }
  15%{
    background-color:yellow ;
  }
  30%{
    background-color:lime;
  }
  45%{
    background-color:lightblue;
  }
  60%{
    background-color:blue;
  }
  75%{
    background-color: purple;
  }
  90%{
    background-color:pink
  }
  100%{
    background-color: red;
  }
}

*{
  margin:0;
  padding:0;
  font-family: 'Lato', sans-serif;
}
*:focus {outline:0;}
.mainWrapper, body, html{
  width:100vw;
  height: 100%;
}
body{
  font-family:'Lato';
  overflow: hidden;
}
.sidebar{
  background-color: #3F0E40;
  position: absolute;
  width:15%;
  height:100%;
  color: white;
  font-family: 'Lato', sans-serif;
  padding-left: 1%;
  padding-right: 1%;
  padding-top: 1%;
}
#sideProfile{
  position: relative;
  width: 100%;
  height: 30%;

}
#sideTop{
  padding-bottom: 20%;
}
#sideProfile h1{
  font-size: 1.5vw;
}
#sideProfile:hover{
  cursor: pointer;

}
#sideProfile p{
  color:rgb(202, 200, 200);
  font-size: 1.5vw;
  position: relative;
  left:10%;
}
#online{
  border-radius:50%;
  width:6%;
  padding-top: 6%;
  top:71%;
  background-color: green;
  position: absolute;
}
#sideProfile h1 span{
  font-size: 1vw;
  position: absolute;
  left:70%;
  top:15%;
  color:rgb(202, 200, 200);
}
.fa{
  width:10%;
  height: 100%;
  position: absolute;
  right:3%;
  top:10%;
  color: rgb(202, 200, 200);
}
.fa:hover{
  color: white;
}
#sideImage{
  background-image: url('https://www.freeiconspng.com/uploads/church-bell-icon-5.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: absolute;
  right:0;
  top:0;
  width: 10%;
  height: 100%;
}
#channels{
  font-size: 1.5vw;
  color: rgb(207,195,207);

}
#channels:hover{
  color: white;
  cursor: pointer;
}
#sideBottom{
  width: 112.5%;
  max-height: 70%;
  position: relative;
  overflow-y: scroll;
  background:transparent;
  display:inline-block;
  left:-6%;
}

.ChatBox{
  width:100%;
  height:5%;
  position:absolute;
  bottom:0;
  right:0;
}

.ChatBox input{
  width:79%;
  position:absolute;
  right:2%;
  padding-top:1%;
  padding-bottom:1%;
  bottom:15%;
  resize:none;
  font-size:15px;
  font-family:'';
  color:black;
}

.sidebar button{
  color:rgb(207,195,207);
  background:transparent;
  border:none;
  font-size:1.5vw;
  width:100%;
  text-align:left;
  padding-top:3%;
  border-top: none;
}
.sidebar button:hover{
  cursor: pointer;
}

.mc{
  width:80%;
  position:absolute;
  right:2%;
  height:80%;
  top:10%;
  
}
#messages{
  position:absolute;
  width:100%;
  max-height:100%;
  overflow-y: auto;
  display: inline-block;

}

#date{
  color:grey;
  font-size:10px;
}

#name{
  font-size:2vh;
  font-weight:bold;
}
#name:hover{
  cursor: pointer;
  text-decoration: underline;
}

#messagevalue{
  position:relative;
  display: inline-block;
  font-size:15px;
  min-width:100%;
  left:1%;

}
#messagevalue:hover{
  background-color: rgba(243, 243, 243, 0.562);
}

.chatButtons{
  display: inline-block;
  padding-bottom:5%;
  width:100%;
  padding-left:5%;
}
.channelNameClass{
  display: inline-block;
  width: 100%;
}
#channels{
  padding-left:3%;
}

.topbar{
  position: absolute;
  right:0;
  width:83%;
  height:7%;
  background-color:rgba(100,100,100,0.2)
}

.topbar h2{
  position: absolute;
  left:0;
  margin-left:3%;
  margin-top:0.8%;
  font-size: 25px;
}

input{
  font-family:'Lato';
}

#topic{
  position: absolute;
  left:0;
  margin-left:70%;
  top:0;
  margin-top:2%;
}

#changetopic{
  position: absolute;
  right:0;
  top:0;
  border:none;
  font-size:15px;
  background-color:transparent;
}

#themes{
  display:none;
  padding-bottom:15%;
  padding-left: 5%;
}
.options button:hover{
  color:white;
}
.options button{
  padding-top:5%;
}
.options h2{
  position: relative;
  left:10%;
  padding-bottom:4%;
}

#emoji{
  font-size:30px;
  right:0;
  bottom:0;
  height:100%;
  width:100%;
  margin-bottom:20%;
  background-color:rgb(224, 217, 217);
  overflow-y:scroll;
}
.emoji p:hover{
  cursor: pointer;
}

#toggleemojis{
  position: absolute;
  right:0;
  bottom:0;
  background-color:black;
  height:50px;
  width:30px;
  margin-bottom:0.5%;
  color:white;
  border:none;
}

#emoji p{
  float:left;

}
#emojiwrapper{
  position: absolute;
  display: none;
  right:0;
  bottom:0;
  width:35vw;
  height:30vh;
  margin-right:2%;
  margin-bottom:5.9%;
  background-color:rgb(224, 217, 217); 
}

#emojibuttons{
  position: relative;
  width: 100%;
  height:10%;
  text-align:center;
}
#emojibuttons button{
  border:none;
  padding: 1%;
  width:10%;
}
#emojibuttons button:hover{
  cursor:pointer
}
